/*Big tablet to pc */

@media only screen and (max-width: 1200px) {
}

/* small tablet to big tablet: from 768px to 1023px */
@media only screen and (max-width: 1023px) {
  body {
    font-size: 18px;
  }

  h1 {
    font-size: 180%;
  }
  h2 {
    font-size: 180%;
  }

  .main-nav li {
    font-size: 100%;
  }
}

/* Small phones to small tablets: from 481px to 767px */
@media only screen and (max-width: 767px) {
  body {
    font-size: 16px;
  }

  .main-nav {
    font-size: 90%;
    display: flex;
    flex-wrap: wrap;
  }

  .main-nav li {
    margin-right: 0px;
    margin-bottom: 25px;
  }

  .intro {
    margin: 0 0 20% 20%;
  }
}

/* this only applies to widths less than or equal to 480px */
/* small phones from 0 to 480px */
@media only screen and (max-width: 480px) {
  section {
    padding: 25px 0;
  }

  .main-nav {
    font-size: 80%;
    display: flex;
    flex-wrap: wrap;
  }

  .main-nav li {
    margin-right: 0px;
    margin-bottom: 25px;
  }
}

